<script>
  import { onMount } from "svelte";
  import { tweened } from "svelte/motion";
  import { quintInOut } from "svelte/easing";
  import { get } from "svelte/store";

  import * as d3 from "d3";

  //prop - list of players to display
  export let playerDataToShow = [];
  export let containerW, containerH;

  const margin = { top: 50, right: 50, bottom: 50, left: 50 };
  let width = window.innerWidth - margin.left - margin.right; // Use the window's width
  let height = window.innerHeight - margin.top - margin.bottom; // Use the window's height

  let xScaleBase = d3
    .scaleTime()
    .domain([new Date(2009, 0, 1), Date.now()]) //data input range
    .range([0, width]); //graph output range

  let yScaleBase = d3
    .scaleLinear()
    .domain([0, 3000])
    .range([height, 0]);

  let innerLineFunc = d3.line().curve(d3.curveStepAfter);
  let ratingLineFunc = data => {
    //console.log("generating line");
    let generatedLine = innerLineFunc(data);
    //console.log(generatedLine.slice(0, 20));
    return generatedLine;
  };

  let zoomedXScale = xScaleBase;
  let zoomedYScale = yScaleBase;

  let yAxis = undefined;
  let xAxis = undefined;

  let clipTransform = "";
  let brush = null;
  let brushGroup = null;
  let resettingBrush = false;
  let svgW, svgH;
  let svgElement;
  let svgChildGElement;
  //let svgRef = null;

  const xAxisGenerator = context => {
    let gen = d3
      .axisBottom(zoomedXScale)
      .ticks(width / 100)
      .tickFormat(d => {
        if( d <= d3.timeYear(d) ){
          return d.getFullYear();
        }else if( d <= d3.timeMonth(d) ){
          return d3.timeFormat("%b")(d);
        }else{
          return d3.timeFormat("%b %d")(d);
        }
      });

    return gen(context);
  };

  let updatePlot = (plotWidth, plotHeight) => {
    width = plotWidth - margin.left - margin.right; // Use the parent element width
    height = plotHeight - margin.top - margin.bottom; // Use the parent element height
    //console.log(`plot: w=${width}, h=${height}`);

    xScaleBase = d3
      .scaleTime()
      .domain([new Date(2009, 0, 1), Date.now()]) //TODO: Pick domain from data
      .range([0, width]);

    yScaleBase = d3
      .scaleLinear()
      .domain([0, 3000]) //TODO: Pick domain from data
      .range([height, 0]);

    //zoomedScales are replaced/updated as user interacts with the brush/zoom
    if (zoomedXScale !== xScaleBase) {
      zoomedXScale = zoomedXScale.range([0, width]);
    }

    if (zoomedYScale !== yScaleBase) {
      zoomedYScale = zoomedYScale.range([height, 0]);
    }

    let svgRef = d3.select(svgChildGElement);

    if (xAxis) {
      xAxis
        .transition()
        .duration(100)
        .attr("transform", `translate(0, ${height})`)
        .call(xAxisGenerator);

      yAxis
        .transition()
        .duration(100)
        .call(d3.axisLeft(zoomedYScale))
        .on("end", () => {
          updateLines(playerDataToShow);
          d3.selectAll(".playerline")
            .transition()
            .duration(1000)
            .style("opacity", 1.0);
        }); //wait until axis has resized to update the plot lines
    }
    if (brush) {
      brush.extent([[0, 0], [width, height]]);
      brushGroup.call(brush);
    }

    d3.selectAll(".playerline")
      .transition()
      .duration(50)
      .style("opacity", 0.3);
  };

  onMount(async () => {
    updatePlot(
      svgElement.parentElement.clientWidth,
      svgElement.parentElement.clientHeight
    );

    let svgRef = d3.select(svgChildGElement);

    xAxis = svgRef
      .append("g")
      .style("font", "16px Roboto")
      //.attr("transform", "translate(0,950)")
      .call(xAxisGenerator);

    yAxis = svgRef
      .append("g")
      .style("font", "16px Roboto")
      //.attr("transform", "translate(50,0)")
      .call(d3.axisLeft(zoomedYScale));

    clipTransform = `translate(${margin.left}, ${margin.top})`;

    // zoom with mouse scroll
    //
    // const zoom = d3
    //   .zoom()
    //   .scaleExtent([1.0, 4.0])
    //   .extent([[0, 0], [1000, 1000]])
    //   .on("zoom", zoomUpdate);

    // const zoomUpdate = () => {
    //   zoomedXScale = d3.event.transform.rescaleX(xScaleBase);
    //   zoomedYScale = d3.event.transform.rescaleY(yScaleBase);

    //   xAxis.call(d3.axisBottom(zoomedXScale));
    //   yAxis.call(d3.axisLeft(zoomedYScale));
    //   linesToShow = linesToShow; //eek
    // }
    // svgRef
    //   .append("rect")
    //   .attr("width", width)
    //   .attr("height", height)
    //   .style("fill", "none")
    //   .style("pointer-events", "all")
    //   .attr("transform", clipTransform)
    //   .call(zoom);

    //zoom via brushing (more touch-friendly)

    const brushUpdate = () => {
      let extent = d3.event.selection;

      if (extent) {
        const minXInterval_ms = 6 * 7 * 24 * 60 * 60 * 1000; // 6 weeks

        //invert pixel extents to get date values
        let start_dt = zoomedXScale.invert(extent[0]);
        let end_dt = zoomedXScale.invert(extent[1]);

        let extentInterval = end_dt - start_dt;

        if (extentInterval < minXInterval_ms) {
          //get mid-point of requested range
          let midPoint = (start_dt.getTime() + end_dt.getTime()) * 0.5;

          start_dt = new Date(midPoint - minXInterval_ms * 0.5);
          end_dt = new Date(midPoint + minXInterval_ms * 0.5);
        }

        //if brush selection exists
        let tempScale = zoomedXScale.copy();

        //rescale domain based on brush X extents
        tempScale = tempScale.domain([start_dt, end_dt]);
        //replace zoomed scale
        zoomedXScale = tempScale;

        //This flag is to catch the recursion so the brush overlay can be cleared
        //TODO: Find some other way to do this? Recursing and catching a flag is kind of ugly.
        resettingBrush = true;

        brushGroup.call(brush.move, null); //this recurses into here
      } else {
        if (!resettingBrush) {
          //This check is to avoid behaviour where the lines
          //will disappear if the scale is unchange and the transitions
          //are still triggered. At some point it would be nice to know
          //why transitioning to the same values is a problem.
          if (zoomedXScale !== xScaleBase) {
            zoomedXScale = xScaleBase; //.copy();
          } else {
            return;
          }
        }
        resettingBrush = false;
      }

      xAxis
        .transition()
        .duration(1000)
        .call(xAxisGenerator);

      updateLines(playerDataToShow);
    };

    //brush overlay definition
    //this catches mouse events and calls the callback with
    //the extents info, i.e. the size of the selection
    brush = d3
      .brushX()
      .extent([[0, 0], [width, height]])
      .on("end", brushUpdate);

    brushGroup = svgRef
      .append("g")
      .attr("class", "brush")
      .call(brush);
  });

  //Creates x, y pair array describing lines, using the provided scales
  function getScaledLineData(playerData, xScale, yScale) {
    let yVals = playerData.map(e => {
      return yScale(e[1]);
    });
    let xVals = playerData.map(e => {
      return xScale(e[0]);
    });

    let zipped = xVals.map((e, i) => {
      return [e, yVals[i]];
    });

    return zipped;
  }

  function playerLineData(pd) {
    return {
      data: d3.zip(
        pd.dt.map(e => {
          return Date.parse(e);
        }),
        pd.rating
      ),
      colour: pd.colour,
      name: pd.name
    };
  }

  let linesToShow = [];
  let displayPaths = {};
  let unsubs = {};

  //This generates a tween update callback
  let getTweenCallback = playerName => {
    let callback = d => {
      //console.log(`Tweening for ${playerName}, first elem = ${d[0]}`);

      //generate svg path string for tweened data positions
      let tweenedPath = ratingLineFunc(d);

      //search for the name in the lines array for now
      for (const l of linesToShow) {
        if (l.name == playerName) {
          l.path = tweenedPath;
          //console.log("tween: " + l.path.slice(0,10));
          break;
        }
      }
      linesToShow = linesToShow; //awful, but svelte demands an assignment
    };

    return callback;
  };

  async function updateLines(playerData) {
    linesToShow = []; //clear the lines array

    for (const pd of playerData) {
      let ld = playerLineData(pd);
      linesToShow.push(ld);
      let path = displayPaths[ld.name];
      let scaledData = getScaledLineData(ld.data, zoomedXScale, zoomedYScale);

      if (path) {
        path.set(scaledData); //if path exists then update the tween data
        ld["path"] = ratingLineFunc(get(path)); //construct path from existing position data
      } else {
        //construct tween store
        path = tweened(scaledData, {
          duration: 1000,
          easing: quintInOut
        });
        //generate svg path string
        ld["path"] = ratingLineFunc(scaledData);

        //subscribe to tween updates and store unsub function
        unsubs[ld.name] = path.subscribe(getTweenCallback(ld.name));
        displayPaths[ld.name] = path;
      }
    }
  }

  $: updateLines(playerDataToShow);

  $: updatePlot(containerW, containerH);
</script>

<style>

</style>

<div style="height: {containerH}px; width: {containerW}px;">
  <svg bind:this={svgElement} translate="0,0" width="100%" height="100%">
    <defs>
      <clipPath id="clip">
        <rect {height} {width} />
      </clipPath>
    </defs>
    <g bind:this={svgChildGElement} {height} {width} transform={clipTransform}>
      <g clip-path="url(#clip)">
        {#each linesToShow as ld, i}
          <path
            class="playerline"
            path
            d={ld.path}
            stroke={ld.colour}
            stroke-width="3"
            fill="none" />
        {/each}
      </g>
    </g>
  </svg>
</div>
