<script>
  import { onMount } from "svelte";
  import PlayerPicker from "./PlayerPicker.svelte";
  import RatingPlot from "./RatingPlot.svelte";
  import { getLineColour, releaseLineColour } from "./colourcache.js";

  let playerIndices = {};
  let playerNames = [];

  let playerRatingDict = {};
  let playerDataToShow = [];

  let plotWidth, plotHeight;

  onMount(async () => {
    //Load top-level player data, individual histories are loaded on-demand
    const res = await fetch(`./data/players.json`);
    let playerTLData = await res.json();
    //sort into descending rating order
    playerTLData.sort((a, b) => {
      return b.latest_rating - a.latest_rating;
    });

    //get the top 50 - will be selectable filter at some point
    playerIndices = {};
    for (let e of playerTLData.slice(0, 50)) {
      playerIndices[e.name] = e;
    }
    playerNames = Object.keys(playerIndices);
    playerNames.sort();
  });

  //Loads individual rating data
  async function loadPlayerData(playerName) {
    const index = playerIndices[playerName]["index"];
    const res = await fetch(`./data/${index}.json`);
    const pd = await res.json();
    pd["name"] = playerName;
    return pd;
  }

  let playerLineColour = {};

  //Handles changes to selected players for display.
  //Loads rating data if needed and allocates/releases colours.
  async function updatePlayerData(players) {
    let retList = [];

    //removals
    for (const l of playerDataToShow) {
      let playerName = l.name;
      if (!players.includes(playerName)) {
        releaseLineColour(playerLineColour[playerName]);
        delete playerLineColour[playerName];
        //ugh... svelte assignment requirement. Find prettier way.
        playerLineColour = playerLineColour;
      }
    }

    for (const p of players) {
      if (!(p in playerRatingDict)) {
        let ld = await loadPlayerData(p);
        playerRatingDict[p] = ld;
      }

      let pd = playerRatingDict[p];

      if (!(p in playerLineColour)) {
        playerLineColour[p] = getLineColour();
        pd["colour"] = playerLineColour[p];
        playerLineColour = playerLineColour;
      }

      retList.push(pd);
    }

    return retList;
  }

  let selectionChanged = event => {
    (async () => {
      let temp = await updatePlayerData(event.detail);
      playerDataToShow = temp;
    })();
    console.log("got new selection: " + event.detail);
  };

  //$: console.log(`w: ${plotWidth}, h: ${plotHeight}`);

</script>

<style>
  .plot-container {
    display: inline-block;
    position: relative;
    width: 100%;
    height: 66vh;
    min-height: 300px;
    vertical-align: middle;
    overflow: hidden;
  }
</style>

<svelte:head>
  <style>
    html {
      background-color: #faf5dc;
    }
  </style>
</svelte:head>

<h2>Glicko 2 Tennis Ratings</h2>
<div class="plot-container" bind:clientWidth={plotWidth} bind:clientHeight={plotHeight}>
  <RatingPlot {playerDataToShow} containerW={plotWidth} containerH={plotHeight} />
</div>

<div class="picker-container">
  <PlayerPicker
    on:selectionChanged={selectionChanged}
    playerList={playerNames}
    playerColours={playerLineColour} 
    maxSelectable=8/>
</div>
