import {schemeDark2} from 'd3-scale-chromatic';
import {scaleOrdinal} from 'd3-scale';

let colourScale = scaleOrdinal(schemeDark2);
let colourCache = [];
let colourCount = 0;

function getLineColour() {
  if (colourCache.length > 0) {
    return colourCache.pop();
  }

  return colourScale(colourCount++);
}

function releaseLineColour(c) {
  colourCache.push(c);
}

export { getLineColour, releaseLineColour };
