<script>
  import { createEventDispatcher } from "svelte";
  import { send, receive } from "./crossfade.js";

  const dispatch = createEventDispatcher();

  //prop - list of players to pick from
  export let playerList = [];
  //prop - lookup for player colours
  export let playerColours = {};
  export let maxSelectable = 0;

  let selectedPlayers = [];
  let selectedLength = 0;

  $: if (selectedLength != selectedPlayers.length) {
    selectedLength = selectedPlayers.length;
    dispatch("selectionChanged", selectedPlayers);
  }

  const selectPlayer = p => {
    if (selectedPlayers.length < maxSelectable) {
      if (selectedPlayers.includes(p)) return; //should this be necessary? Getting double selections.
      playerList = playerList.filter(entry => entry !== p);
      selectedPlayers.push(p);
      selectedPlayers.sort();
      selectedPlayers = selectedPlayers;
    }
  };

  const deselectPlayer = p => {
    selectedPlayers = selectedPlayers.filter(entry => entry !== p);
    playerList.push(p);
    playerList.sort();
  };
</script>

<style>
  .pp_container {
    display: flex;
    flex-direction: column;
  }

  .selector_button {
    margin: 2px;
  }

  .selected_button {
    margin: 2px;
  }

  .pp_selector_div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-height: 200px;
    overflow-y: auto;
  }
  .pp_selected_div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
</style>

<div class="pp_container">
  <h3>Selected Players</h3>
  <div class="pp_selected_div">
    {#each selectedPlayers as player (player)}
      <label
        in:receive={{ key: player }}
        out:send={{ key: player }}
        on:click={() => deselectPlayer(player)}>
        <button
          class="selected_button"
          type="button"
          style="background-color:{playerColours[player]}">
          {player}
        </button>
      </label>
    {/each}

  </div>
  <h3>Select Players ({maxSelectable} max)</h3>
  <div class="pp_selector_div">
    {#each playerList as player (player)}
      <label
        in:receive={{ key: player }}
        out:send={{ key: player }}
        on:click={() => selectPlayer(player)}>
        <button class="selector_button" type="button">{player}</button>
      </label>
    {/each}
  </div>

</div>
